
.navbar {
  position: relative;
  width: 100%;
  max-width: $grid-max-width;
  margin: 0.5rem auto 36px auto;
  text-rendering: optimizeLegibility;
  @include hyphens(none);
}

.navbar__inner {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: flex-end;
  margin-left: ($grid-gutter_width / 2);
  margin-right: ($grid-gutter_width / 2);
}

//.navbar__icon {
  //width: 50px;
  //height: 50px;
  //background: url("../img/site_icon.jpg") 0 0 no-repeat;
  //background-size: contain;
//}

.navbar__title {
  order: 1;
  width: 100%;
  white-space: nowrap;
  border-bottom: $nav-border-width solid $black;
  padding-bottom: 1px;
  margin-top: 11px;
  margin-right: auto;
  font-size: $navbar-brand-font-size;
  text-transform: uppercase;
  line-height: 1.2em;
  letter-spacing: 0.03em;

  a {
    color: $nav-text-color;
    text-decoration: none;

    &:focus,
    &:hover,
    &:visited {
      color: $nav-text-color;
      text-decoration: none;
    }
    &:visited {
      color: $nav-text-color;
    }
    &:hover {
      color: $nav-link-hover-color;
    }
  }
}

.navbar .nav {
  order: 2;
  flex-grow: 2;
  // Auto maximises space between links and search:
  margin: $nav-border-width auto 0 0;
}

.navbar__form {
  /* At narrowest widths, navbar__form goes underneath navbar__title. */
  order: 3;
  flex-grow: 2;
  align-self: flex-start;
  padding-bottom: $nav-border-width;
  border-bottom: $nav-border-width solid $black;
  margin-bottom: $nav-border-width;

  form {
    float: right;
  }

  .form--inline .form__control {
    width: 8em;
    margin-right: ($nav-border-width / 2);
  }

  // Hide the form at narrow widths if the JS is enabled:
  body.js-can-navbar & {
    display: none;
  }

  //input {
    //background: blue;
  //}
}

.js-navbar-nav-item-search {
  text-transform: uppercase;
}


@media (min-width: 510px) {
  /* Form is to right of title. Nav is underneath. */

  .navbar__title {
    width: auto;
  }

  .navbar .nav {
    order: 3;
  }

  .navbar__form {
    order: 2;
    margin-bottom: 0;
    padding-bottom: $nav-border-width;

    body.js-can-navbar & {
      display: block;
    }
  }

  .js-navbar-item-search {
    display: none;
  }

  //.navbar__form input {
    //background: green;
  //}

}

@media (min-width: 890px) {
  /* Form is to right of title. Nav is in line with title's bottom border. */

  .navbar {
    /* Replace margin we're taking off the nav__item by adding it to the .navbar's, which is usually $nav-border-width. */
    margin-bottom: (36px + $nav-border-width);
  }

  .navbar__title {
    width: auto;
    margin-right: $nav-link-padding-x;
    margin-top: 10px;
  }

  .navbar .nav {
    order: 2;
    width: auto;

    .nav__item {
      margin-bottom: 0;
    }
  }

  .navbar__form {
    position: absolute;
    top: 0;
    right: ($grid-gutter-width / 2);
    border-bottom: none;
    padding-top: 0;
  }

  //.navbar__form input {
    //background: yellow;
  //}
}

